<template>
  <div class="register">
    <el-row>
      <el-col :xs="{span:22,offset:1}" :sm="{span:12,offset:6}" :md="{span:6,offset:9}">
        <h1></h1>
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label="" prop="username">
            <el-input v-model="form.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="form.password" type="password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password2">
            <el-input v-model="form.password2" type="password"></el-input>
          </el-form-item>
          <el-form-item>
            
            <router-link to="/login">
              <el-button type="text"></el-button>
            </router-link>
            <el-button type="primary" class="pull-right" @click="submitRegister('form')"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script type="text/ecmascript-6">
  import { CommonService } from '../../service/CommonService';
  import { UserService } from '../../service/UserService';

  export default {
    created () {
      CommonService.isLogin(this.$store, () => {
        this.$router.push('main');
      });
    },
    methods: {
      submitRegister (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            UserService.register(this.form, () => {
              this.$message.success('');
              this.$router.push('/login');
            }, (error) => {
              this.$message.error(error);
            });
          } else {
            this.$message({
              message: '',
              type: 'error'
            });
          }
        });
      }
    },
    data () {
      var checkPassword2 = (rule, value, callback) => {
        if (value !== this.form.password) {
          return callback(new Error(''));
        } else {
          callback();
        }
      };
      var checkUsername = (rule, value, callback) => {
        UserService.checkUsername({username: value}, callback, () => {
          callback(new Error(''));
        });
      };
      return {
        form: {
          username: '',
          password: '',
          password2: ''
        },
        rules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {min: 3, max: 10, message: '3-10', trigger: 'change'},
            {validator: checkUsername, trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'change'}
          ],
          password2: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkPassword2, trigger: 'change'}
          ]
        }
      };
    }
  };
</script>

<style lang="stylus" ref="stylesheet/stylus" scoped>
</style>



